.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: white;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    transform: translatex(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2{
        padding: 2rem;
    }
}

.library-song{
    display: flex;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    transition: background 0.5s ease;
    img{
        width: 30%;
    }
    &:hover{
        background: rgb(221, 244, 252);
    }
}
.song-description{
    padding: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7;
    }
}
*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155,0.5) transperent;
}
*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar{
    background: transparent;
}
*::-webkit-scrollbar-thumb{
    background-color: rgba(155,155,155,0.5) ;
    border-radius: 20px;
    border:transparent;
}

.selected{
    background-color: rgb(208, 211, 226);
}
.active-library{
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library{
        width: 100%;
    }
}   